"use client";

import { useState, useRef, useEffect } from 'react';
import { Moon, Sun, Trash2, Settings, Paperclip, Send, Sparkles } from 'lucide-react';

interface Message {
  id: number;
  text: string;
  isUser: boolean;
  timestamp: Date;
}

type QuickActionType = 'ideas' | 'summarize' | 'explain';

export default function ChatPage() {
  const [messages, setMessages] = useState<Message[]>([
    { 
      id: 1, 
      text: "Hello! I'm your AI assistant. How can I help you today?", 
      isUser: false, 
      timestamp: new Date() 
    }
  ]);
  const [inputMessage, setInputMessage] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [isDarkMode, setIsDarkMode] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSendMessage = () => {
    if (!inputMessage.trim()) return;

    const userMessage: Message = {
      id: messages.length + 1,
      text: inputMessage,
      isUser: true,
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputMessage('');
    setIsLoading(true);

    setTimeout(() => {
      const aiResponse: Message = {
        id: messages.length + 2,
        text: "I understand your question. This is a simulated response from the AI assistant. How can I help you further?",
        isUser: false,
        timestamp: new Date()
      };
      setMessages(prev => [...prev, aiResponse]);
      setIsLoading(false);
    }, 1500);
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  const handleQuickAction = (action: QuickActionType) => {
    const quickMessages: Record<QuickActionType, string> = {
      ideas: "Can you suggest some creative ideas for my project?",
      summarize: "Please summarize the key points from our conversation.",
      explain: "Can you explain how this AI assistant works?"
    };
    
    setInputMessage(quickMessages[action]);
  };

  const formatTime = (date: Date) => {
    return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
  };

  const handleClearChat = () => {
    setMessages([
      { 
        id: 1, 
        text: "Hello! I'm your AI assistant. How can I help you today?", 
        isUser: false, 
        timestamp: new Date() 
      }
    ]);
  };

  return (
    <div className={`flex flex-col h-screen transition-colors duration-300 ${isDarkMode ? 'bg-gray-900' : 'bg-gradient-to-br from-blue-50 via-purple-50 to-pink-50'}`}>
      {/* Header */}
      <div className={`border-b px-6 py-4 flex items-center justify-between backdrop-blur-sm transition-colors duration-300 ${
        isDarkMode ? 'bg-gray-800/95 border-gray-700' : 'bg-white/80 border-gray-200'}`}>
        <div className="flex items-center space-x-4">
          <div className="relative">
            <div className="w-12 h-12 bg-gradient-to-r from-blue-500 via-purple-600 to-pink-500 rounded-full flex items-center justify-center shadow-lg animate-pulse">
              <Sparkles className="w-6 h-6 text-white" />
            </div>
            <div className="absolute -bottom-1 -right-1 w-4 h-4 bg-green-500 rounded-full border-2 border-white dark:border-gray-800"></div>
          </div>
          <div>
            <h2 className={`text-lg font-bold ${isDarkMode ? 'text-white' : 'text-gray-800'}`}>
              AI Assistant
            </h2>
            <div className="flex items-center space-x-2">
              <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse"></div>
              <span className={`text-sm ${isDarkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                Online
              </span>
            </div>
          </div>
        </div>
        
        <div className="flex items-center space-x-2">
          <button onClick={() => setIsDarkMode(!isDarkMode)} className={`p-2.5 rounded-lg transition-all duration-300 ${isDarkMode ? 'bg-gray-700 hover:bg-gray-600 text-yellow-400' : 'bg-gray-100 hover:bg-gray-200 text-gray-700'}`} title="Toggle theme">
            {isDarkMode ? <Sun className="w-5 h-5" /> : <Moon className="w-5 h-5" />}
          </button>
          <button onClick={handleClearChat} className={`p-2.5 rounded-lg transition-all duration-300 ${isDarkMode ? 'bg-gray-700 hover:bg-gray-600 text-gray-300' : 'bg-gray-100 hover:bg-gray-200 text-gray-700' }`} title="Clear chat">
            <Trash2 className="w-5 h-5" />
          </button>
        </div>
      </div>

      {/* Messages Area */}
      <div className="flex-1 overflow-y-auto px-4 py-6 space-y-6">
        {messages.map((message) => (
          <div
            key={message.id}
            className={`flex items-start space-x-3 animate-fade-in ${
              message.isUser ? 'flex-row-reverse space-x-reverse' : ''
            }`}
          >
            {/* Avatar */}
            <div className={`flex-shrink-0 w-10 h-10 rounded-full flex items-center justify-center text-xs font-semibold shadow-md transition-transform hover:scale-110 ${
              message.isUser 
                ? 'bg-gradient-to-r from-blue-500 to-blue-600 text-white' 
                : 'bg-gradient-to-r from-purple-500 to-pink-500 text-white'
            }`}>
              {message.isUser ? 'üë§' : 'ü§ñ'}
            </div>

            {/* Message Content */}
            <div className={`max-w-[70%] ${
              message.isUser ? 'text-right' : ''
            }`}>
              <div className={`inline-block px-5 py-3 rounded-2xl shadow-md transition-all hover:shadow-lg ${
                message.isUser
                  ? 'bg-gradient-to-r from-blue-500 to-blue-600 text-white rounded-br-none'
                  : isDarkMode
                    ? 'bg-gray-800 border border-gray-700 text-gray-100 rounded-bl-none'
                    : 'bg-white border border-gray-200 text-gray-800 rounded-bl-none'
              }`}>
                <p className="text-sm leading-relaxed">{message.text}</p>
              </div>
              <div className={`text-xs mt-1.5 ${
                isDarkMode ? 'text-gray-500' : 'text-gray-500'
              } ${message.isUser ? 'text-right' : ''}`}>
                {formatTime(message.timestamp)}
              </div>
            </div>
          </div>
        ))}
        
        {/* Loading Indicator */}
        {isLoading && (
          <div className="flex items-start space-x-3 animate-fade-in">
            <div className="flex-shrink-0 w-10 h-10 rounded-full bg-gradient-to-r from-purple-500 to-pink-500 flex items-center justify-center text-xs font-semibold text-white shadow-md">
              ü§ñ
            </div>
            <div className={`rounded-2xl rounded-bl-none px-5 py-4 shadow-md ${
              isDarkMode ? 'bg-gray-800 border border-gray-700' : 'bg-white border border-gray-200'
            }`}>
              <div className="flex space-x-1.5">
                <div className={`w-2.5 h-2.5 rounded-full animate-bounce ${
                  isDarkMode ? 'bg-gray-500' : 'bg-gray-400'
                }`}></div>
                <div className={`w-2.5 h-2.5 rounded-full animate-bounce ${
                  isDarkMode ? 'bg-gray-500' : 'bg-gray-400'
                }`} style={{ animationDelay: '0.1s' }}></div>
                <div className={`w-2.5 h-2.5 rounded-full animate-bounce ${
                  isDarkMode ? 'bg-gray-500' : 'bg-gray-400'
                }`} style={{ animationDelay: '0.2s' }}></div>
              </div>
            </div>
          </div>
        )}
        
        <div ref={messagesEndRef} />
      </div>

      {/* Quick Actions & Input Area */}
      <div className={`border-t px-4 py-4 backdrop-blur-sm transition-colors duration-300 ${
        isDarkMode 
          ? 'bg-gray-800/95 border-gray-700' 
          : 'bg-white/80 border-gray-200'
      }`}>
        <div className="flex flex-wrap gap-2 mb-4">
          <button
            onClick={() => handleQuickAction('ideas')}
            className={`px-4 py-2 text-xs font-medium rounded-full transition-all hover:scale-105 shadow-sm ${
              isDarkMode 
                ? 'bg-gray-700 hover:bg-gray-600 text-gray-300 border border-gray-600' 
                : 'bg-gradient-to-r from-blue-100 to-purple-100 hover:from-blue-200 hover:to-purple-200 text-gray-700 border border-gray-300'
            }`}
          >
            üí° Suggest ideas
          </button>
          <button
            onClick={() => handleQuickAction('summarize')}
            className={`px-4 py-2 text-xs font-medium rounded-full transition-all hover:scale-105 shadow-sm ${
              isDarkMode 
                ? 'bg-gray-700 hover:bg-gray-600 text-gray-300 border border-gray-600' 
                : 'bg-gradient-to-r from-purple-100 to-pink-100 hover:from-purple-200 hover:to-pink-200 text-gray-700 border border-gray-300'
            }`}
          >
            üìù Summarize
          </button>
          <button
            onClick={() => handleQuickAction('explain')}
            className={`px-4 py-2 text-xs font-medium rounded-full transition-all hover:scale-105 shadow-sm ${
              isDarkMode 
                ? 'bg-gray-700 hover:bg-gray-600 text-gray-300 border border-gray-600' 
                : 'bg-gradient-to-r from-pink-100 to-orange-100 hover:from-pink-200 hover:to-orange-200 text-gray-700 border border-gray-300'
            }`}
          >
            üîç Explain
          </button>
        </div>

        {/* Input Area */}
        <div className="flex space-x-3">
          <div className="flex-1 relative">
            <input
              type="text"
              value={inputMessage}
              onChange={(e) => setInputMessage(e.target.value)}
              onKeyPress={handleKeyPress}
              placeholder="Type your message..."
              className={`w-full px-5 py-3.5 rounded-full focus:outline-none focus:ring-2 focus:ring-blue-500 pr-12 transition-all shadow-sm ${
                isDarkMode 
                  ? 'bg-gray-700 border border-gray-600 text-white placeholder-gray-400' 
                  : 'bg-white border border-gray-300 text-gray-800 placeholder-gray-500'
              }`}
              disabled={isLoading}
            />
            <button 
              type="button" 
              className={`absolute right-4 top-1/2 transform -translate-y-1/2 transition-colors ${
                isDarkMode ? 'text-gray-400 hover:text-gray-300' : 'text-gray-400 hover:text-gray-600'
              }`}
            >
              <Paperclip className="w-5 h-5" />
            </button>
          </div>
          <button
            onClick={handleSendMessage}
            disabled={!inputMessage.trim() || isLoading}
            className="w-14 h-14 bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700 disabled:from-gray-400 disabled:to-gray-500 disabled:cursor-not-allowed rounded-full flex items-center justify-center text-white transition-all shadow-lg hover:shadow-xl hover:scale-105 disabled:hover:scale-100"
          >
            <Send className="w-5 h-5" />
          </button>
        </div>
        
        {/* Footer Note */}
        <div className="text-center mt-3">
          <p className={`text-xs ${isDarkMode ? 'text-gray-500' : 'text-gray-500'}`}>
            AI assistant may produce inaccurate information
          </p>
        </div>
      </div>

      <style jsx>{`
        @keyframes fade-in {
          from {
            opacity: 0;
            transform: translateY(10px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
        .animate-fade-in {
          animation: fade-in 0.3s ease-out;
        }
      `}</style>
    </div>
  );
}